@import "vars";
@import "mixins";
@import "fonts";
@import "settings";



.headline-left{
    padding-left: 103px;
    position: relative;
    font-size: 22px;
    opacity: 0.5;
    
    letter-spacing: 7.33333px;
    line-height: 32px;
    text-transform: uppercase;
    display: inline-block;
    &::before{
        content: "";
        position: absolute;
        width: 80px;
        height: 1px;
        background-color: #fff;
        left: 0;
        top: 50%;
    }
}
.headline-right{
  padding-right: 103px;
  position: relative;
  font-size: 22px;
  opacity: 0.5;
  letter-spacing: 7.33333px;
  line-height: 32px;
  text-transform: uppercase;
  display: inline-block;
  &::before{
      content: "";
      position: absolute;
      width: 80px;
      height: 1px;
      background-color: #fff;
      right: 0;
      top: 50%;
  }
}

.title{
    span {
      color: rgba(#fff, .02);
      font-weight: 800;
      font-size: 200px;
    
      position: relative;
      display: inline-block;
      transform: translateX(-50px);
    }
    h3 {
      position: absolute;
      top: 0;
      right: -25px;
      margin: 0;
      font-size: 80px;
      color:#fff;
  
      &::after{
        content: "";
        position: absolute;
        width: 80px;
        top: 0;
        right: 0;
        height: 1px;
        background-color: #4AF6CD;
      }
    }
  }
  .subtitle {
      margin-top: -86px;
      margin-right: 80px;
      text-align: right;
      &__head{
        font-weight: 800;
        font-size: 40px;
        padding-right: 8px;

        span{
            font-weight: 100;
        }
      }
  }
  .descr{
    padding-left: 5px;
    &__head{
      font-weight: 800;
      font-size: 40px;
      display: flex;
      align-items: center;

      span{
          font-weight: 100;
      }
      &--shop{
        max-width: 340px;
        margin-bottom: 47px;
      }
    }
  }
  .btn {
    font-weight: 800;
  font-size: 40px;
  line-height: 36px;
  span{
    margin-left: 19px;
    padding: 17px 57px 15px 17px;
    background-color: #4AF6CD;
    display: inline-block;
    position: relative;

    &::before{
      z-index: 5;
      content: "";
      position: absolute;
      width: 20px;
      height: 1px;
      background-color: #fff;
      top: 50%;
      left: -10px;
    }
    &::after{
      content: "";
      position: absolute;
      width: 25px;
      height: 21px;
      background-image: url(/img/arrows-right.svg);
      right: 17px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  }
  .surf,
  .holder {
    padding: 160px 0 120px;
  }
  .holder{
    padding-bottom: 300px;
  }





  .quantity {
    display: inline-block;
    
  }
  
  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button
  {
    -webkit-appearance: none;
    margin: 0;
  }
  
  input[type=number]
  {
    -moz-appearance: textfield;
  }
  
  .quantity input {
    width: 33px;
    height: 42px;
    
    display: block;
    border:none;
    color:#fff;
    font-weight: 800;
    font-size: 40px;
    line-height: 36px;
   
    background-color: transparent;
    
    
    
  }
  
  .quantity input:focus {
    outline: 0;
  }
  
  .quantity-nav {
    float: left;
    position: static;
    height: 42px;
    
  }
  
  .quantity-button {
    position: relative;
    cursor: pointer;
    width: 20px;
    text-align: center;
    transform: translateX(-100%);
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
  }
  
  .quantity-button.quantity-up {
    position: absolute; 
    top: 50px;
    right: -20px;
    height: 20px;
  }
  
  .quantity-button.quantity-down {
    position: absolute;
    bottom: -1px;
    top: 50px;
    height: 20px;
    right: 10px;
  }
  